.videoFooter {
    position:relative;
    bottom: 25%;
    color: white;
    margin-left: 25px;
}

.videoFooter__text  h3 {

    padding-bottom: 20px;
}

.videoFooter__text  p {

    padding-bottom: 20px;
    
}


.videoFooter__music {
    display: flex;
}

.videoFooter__icon {
    
}

.videoFooterMusic__text {
    width: 77%;
    overflow-x: hidden;
}

.videoFooterMusic__text  p {
    animation: moveText 4s infinite linear;
}

.videoFooter__record {
    animation: runningRecord 3s infinite linear;
    position: absolute;
    bottom: 5px;
    right: 20px;
    height: 50px;
    filter: invert(1)
}

@keyframes runningRecord {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

@keyframes moveText {
    0% {
        transform: translateX(100%);
    }
    100% {
        transform: rotate(360deg);

    }
}

@media only screen and (max-width: 440px) {
    .videoFooter {
      bottom: 22%;
    }
    .videoFooterMusic__text {
        width: 78%;
    }

}
